body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.Portrait {
  height: 50vmin;
  border-radius: 10px;
  margin-bottom: 40px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.link-item {
    display: flex;
    margin: 15px;
    padding-top: 3px;
    width: 44px;
    height: 44px;
    align-items: center;
    justify-content: center;
    border-radius: 999px;
}

.link-item:hover {
    background-color: #555;
    box-shadow: 0 0 10px 8px #555;
    transition: all 0.2s ease-in-out;
}

.link-item img {
    width: 40px;
}
.bio {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  justify-content: center;
  align-items: center;
  color: white;
}

.bio-name {
  margin: 15px;
  font-size: 40px;
  font-weight: 400;
  color: #fff;
  text-shadow: black 0 2px 6px;
}

.bio-links {
  display: flex;
  flex-direction: row;
}

.portrait {
  max-width: 250px;
  max-height: 250px;
  border-radius: 999px;
  box-shadow: 2px 2px 16px 0px #333;
}

.bio-table-contents {
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  bottom: 20px;
}

.contentLink {
  margin: 20px;
  font-size: 20px;
  font-weight: bolder;
  color: #fff;
  text-decoration: none;
  text-shadow: black 0 1px 1px;
}

.contentLink:visited {
  color: #fff;
}

.contentLink:hover,
.contentLink:active {
  color: #555;
}

@media only screen and (max-width: 750px) {
  .portrait {
      max-width: 30vw;
      max-height: 30vw;
  }

  .contentLink {
      font-size: 1.8vw
  }
}

@media only screen and (max-width: 500px) {
  .portrait {
      max-width: 60vw;
      max-height: 60vw;
  }

  .contentLink {
      font-size: 1.5vw
  }
}
.experience {
  display: flex;
  position: relative;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  justify-content: center;
  align-items: center;
  color: white;
}

.experience-title {
  position: absolute;
  top: 20px;
  margin: 15px;
  font-size: 40px;
  font-weight: 400;
  color: #fff;
  text-shadow: black 0 2px 6px;
}

.experience-content {
  display: flex;
  flex-direction: row;
}
.experience-item {
	display: flex;
	flex-direction: column;
	height: 64vh;
  width: 28vw;
  margin: 20px;
  align-items: center;
  border-radius: 14px;
	background-color: white;
  box-shadow: 2px 2px 16px 0px #333;
}

.experience-item img {
  display: block;
  height: 60px;
  width: auto;
  margin: 10px;
}

.exp-item-pos-date {
  display: flex;
  flex-direction: column;
	color: black;
}

.exp-item-pos-date p {
  margin: 2px;
}

#position {
  font-size: 18px;
  font-weight: bold;
}

.desc-list {
