.bio {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  justify-content: center;
  align-items: center;
  color: white;
}

.bio-name {
  margin: 15px;
  font-size: 40px;
  font-weight: 400;
  color: #fff;
  text-shadow: black 0 2px 6px;
}

.bio-links {
  display: flex;
  flex-direction: row;
}

.portrait {
  max-width: 250px;
  max-height: 250px;
  border-radius: 999px;
  box-shadow: 2px 2px 16px 0px #333;
}

.bio-table-contents {
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  bottom: 20px;
}

.contentLink {
  margin: 20px;
  font-size: 20px;
  font-weight: bolder;
  color: #fff;
  text-decoration: none;
  text-shadow: black 0 1px 1px;
}

.contentLink:visited {
  color: #fff;
}

.contentLink:hover,
.contentLink:active {
  color: #555;
}

@media only screen and (max-width: 750px) {
  .portrait {
      max-width: 30vw;
      max-height: 30vw;
  }

  .contentLink {
      font-size: 1.8vw
  }
}

@media only screen and (max-width: 500px) {
  .portrait {
      max-width: 60vw;
      max-height: 60vw;
  }

  .contentLink {
      font-size: 1.5vw
  }
}